@import url(//fonts.googleapis.com/earlyaccess/notosansjp.css);

/* WordPress Native Styles */
strong {
  font-weight: bold;
}
em {
  font-style: italic;
}
blockquote {
  display: block;
  -webkit-margin-before: 1em;
  -webkit-margin-after: 1em;
  -webkit-margin-start: 40px;
  -webkit-margin-end: 40px;
}
img.alignleft {
  margin: 0 auto 0 0;
}
img.aligncenter {
  display: block;
  margin: 0 auto;
}
img.alignright {
  display: block;
  margin: 0 0 0 auto;
}
img[class*="wp-image-"],
img[class*="attachment-"] {
  height: auto;
  max-width: 100%;
}

/*ALL*/
body {
  color: #111;
  font-size: 16px;
  line-height: 1.8;
  font-family: "Noto Sans JP", "游ゴシック Medium", "Yu Gothic Medium", "游ゴシック", "Yu Gothic", "游ゴシック体",
    "YuGothic", "Hiragino Kaku Gothic ProN", "ヒラギノ角ゴ ProN W3", Meiryo, "メイリオ", Osaka, "MS PGothic", arial,
    helvetica, sans-serif;
  -webkit-text-size-adjust: 100%;
}
@media all and (-ms-high-contrast: none) {
  *::-ms-backdrop,
  body {
    font-family: "Noto Sans JP", Meiryo, "メイリオ", "Hiragino Kaku Gothic ProN", "ヒラギノ角ゴ ProN W3", Osaka,
      "MS PGothic", arial, helvetica, sans-serif;
  }
}
a,
a::before,
a::after {
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  -moz-transition: all 0.3s;
  transition: all 0.3s;
}
a,
a:hover,
a:focus {
  text-decoration: none;
}
a:hover {
  opacity: 0.7;
}
a.i_block {
  vertical-align: bottom;
}

/*pagenation*/
.pagination {
  display: block;
  position: relative;
  margin-top: 45px;
  margin-bottom: 0;
  font-size: 16px;
  line-height: 13px;
  text-align: center;
  font-weight: bold;

  @include mq(xs) {
    margin-top: 30px;
  }
}
.pagination span,
.pagination a {
  display: inline-block;
  margin: 0 3px;
  padding: 12px;
  background: #fff;
  border: 1px solid map-get($color, "dgray");
  color: map-get($color, "black");
  line-height: 1;
}
.pagination a:hover {
  background-color: map-get($color, "border");
}
.pagination .current {
  padding: 12px;
  color: #fff;
  background-color: map-get($color, "dgray");
  border: 1px solid map-get($color, "dgray");
}
/*pagenation END*/

/* google map */
.ggmap {
  position: relative;
  padding-bottom: 23%;
  padding-top: 30px;
  height: 0;
  overflow: hidden;

  iframe,
  object,
  embed {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: none;
  }

  @include mq(xs) {
    padding-bottom: 50%;
  }
}
/* google map END */

/*#########################################
				COMMON
#########################################*/
.box_pd_xs {
  padding-top: 30px;
  padding-bottom: 30px;
}
.box_pd_sm {
  padding-top: 45px;
  padding-bottom: 45px;
}
.box_pd_md {
  padding-top: 60px;
  padding-bottom: 60px;
}
.box_pd_lg {
  padding-top: 90px;
  padding-bottom: 90px;
}
.box_pd_xl {
  padding-top: 120px;
  padding-bottom: 120px;
}
.box_pdt0 {
  padding-top: 0;
}
.box_pdb0 {
  padding-bottom: 0;
}
@include mq(xs) {
  /* COMMON */
  .box_pd_sm,
  .box_pd_md {
    padding-top: 30px;
    padding-bottom: 30px;
  }
  .box_pd_lg,
  .box_pd_xl {
    padding-top: 45px;
    padding-bottom: 45px;
  }
  .box_pdt0,
  .spbox_pdt0 {
    padding-top: 0;
  }
  .box_pdb0,
  .spbox_pdb0 {
    padding-bottom: 0;
  }
  /* COMMON END */
}

.mg_md {
  margin-top: 60px;
  margin-bottom: 60px;

  @include mq(xs) {
    margin-top: 30px;
    margin-bottom: 30px;
  }
}

.maxw740 {
  width: 100%;
  max-width: 740px;
}
.maxw860 {
  width: 100%;
  max-width: 860px;
}

.ul_deco {
  margin-bottom: 0;
  padding: 0;
  list-style: none;

  > li {
    position: relative;
    padding-left: 30px;

    .deco {
      position: absolute;
      left: 5px;
    }
  }

  @include mq(xs) {
    > li {
      position: relative;
      padding-left: 20px;

      .deco {
        position: absolute;
        left: 0;
      }
    }
  }
}

.times {
  font-family: "Times New Roman", Times, serif;
}

.sec_title {
  margin-top: 0;
  margin-bottom: 60px;
  text-align: center;
  font-weight: normal;
  font-size: 36px;
  line-height: 1.2;
  letter-spacing: 2px;

  &.left {
    text-align: left;
  }

  &.mgb_sm {
    margin-bottom: 40px;
  }
  &.mgb_xs {
    margin-bottom: 30px;
  }

  .jp {
    margin-top: 5px;
    display: block;
    font-weight: bold;
    font-size: 16px;
    color: map-get($color, "dgray");
  }

  @include mq(xs) {
    margin-bottom: 30px;
    font-size: 24px;
    letter-spacing: 1px;

    &.mgb_sm,
    &.mgb_xs {
      margin-bottom: 20px;
    }

    .jp {
      font-size: 14px;
    }
  }
}

.btn_clear {
  display: block;
  width: 100%;
  max-width: 460px;
  margin-left: auto;
  margin-right: auto;
  padding: 20px 10px;
  background-color: #fff;
  border: 1px solid map-get($color, "lgray");
  text-align: center;
  font-size: 16px;
  color: map-get($color, "lblack");
  line-height: 1.2;
  letter-spacing: 1px;
  transition: 0.3s;

  &.sm {
    max-width: 380px;
  }

  &:hover,
  &:focus {
    opacity: 1;
    background-color: map-get($color, "lblack");
    border-color: map-get($color, "lblack");
    color: #fff;
  }

  @include mq(xs) {
    max-width: 230px;
    padding: 15px 10px;
    font-size: 14px;

    &.sm {
      max-width: 180px;
    }
  }
}
