
.sub_key {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 200px;
	padding: 30px 15px;
	background: map-get($color, 'beige');
	text-align: center;
	letter-spacing: 2px;
	.title {
		margin: 0;
		font-size: 30px;
		line-height: 1.2;
		font-weight: normal;
		.sub {
			margin-top: 5px;
			display: block;
			font-size: 16px;
			color: map-get($color, 'dgray');
			font-weight: bold;
		}
	}

	@include mq(xs) {
		height: 110px;
		padding: 30px 15px;
		.title {
			font-size: 22px;
			.sub {
				margin-top: 0;
				font-size: 12px;
			}
		}
	}
}

.ul_flex {
	margin-left: -10px;
	margin-right: -10px;
	display: flex;
	flex-wrap: wrap;
	> li {
		flex-basis: auto;
		width: 50%;
		padding: 20px 10px 0;
		&.w100p {
			width: 100%;
		}
		> a {
			position: relative;
			display: flex;
			justify-content: center;
			align-items: center;
			width: 100%;
			height: 100%;
			min-height: 60px;
			padding: 5px 30px;
			border: 1px solid map-get($color, 'border');
			font-size: 16px;
			color: map-get($color, 'black');
			font-weight: bold;
			line-height: 1.2;
			letter-spacing: 1px;

			&::before {
				content: '\f105';
				font-family: 'FontAwesome';
				display: block;
				position: absolute;
				top: 0;
				bottom: 0;
				right: 15px;
				margin: auto;
				color: map-get($color, 'dgray');
				font-size: 16px;
				height: 16px;
				line-height: 1;
				font-weight: normal;
			}
			&::after{
				content: '';
				min-height: 0;
				font-size: 0;
			}
			&:hover, &.focus {
				opacity: 1;
				background-color: map-get($color, 'dgray');
				color: #fff;
				&::before {
					color: #fff;
				}
			}
		}
	}

	@include mq(xs) {
		max-width: 420px;
		margin-left: auto;
		margin-right: auto;
		> li {
			width: 100%;
			padding: 15px 0 0;
			&:first-child {
				padding-top: 0;
			}
			> a {
				min-height: 55px;
				padding: 5px 15px;
				font-size: 14px;

				&::before {
					right: 5px;
				}
			}
		}
	}
}

.title_page {
	position: relative;
	margin-top: 0;
	padding: 5px 5px 8px 15px;
	border-bottom: 1px solid map-get($color, 'lgray');
	font-size: 24px;
	color: map-get($color, 'lblack');
	line-height: 1.2;
	font-weight: bold;
	&::before {
		content: '';
		display: block;
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		margin: auto;
		width: 4px;
		height: 20px;
		background-color: map-get($color, 'dgray');
	}
	&.sm {
		font-size: 20px;
		&::before {
			background-color: map-get($color, 'lgray');
		}
	}

	@include mq(xs) {
		padding: 5px 5px 5px 15px;
		font-size: 18px;
		&::before {
			height: 15px;
		}
		&.sm {
			font-size: 16px;
		}
	}
}

.box_text {
	font-size: 16px;

	p + h2, ul + h2 {
		margin-top: 45px;
	}

	h2 {
		position: relative;
		padding-left: 30px;
		padding-bottom: 5px;
		border-bottom: 1px solid map-get($color, 'border');
		font-size: 18px;
		.num {
			position: absolute;
			left: 0;
		}
	}

	@include mq(xs) {
		font-size: 14px;

		p + h2, ul + h2 {
			margin-top: 30px;
		}

		h2 {
			padding-left: 25px;
			font-size: 16px;
		}
	}
}


@include mq(xs) {
	.sp_table {
		th, td {
			display: block !important;
			width: 100% !important;
		}
	}
}

.require {
	margin-left: 10px;
	color: map-get($color, 'red');
}
.contact_table {
	&.table {
		margin-bottom: 45px;
		tr {
			> th, > td {
				padding: 15px 12px;
			}
			> th {
				letter-spacing: 1px;
				white-space: nowrap;
			}
			> td {
				.form-control {
					border-color: map-get($color, 'lgray');
				}
			}
		}
	}
	@include mq(xs) {
		&.table {
			margin-bottom: 20px;
			tr {
				> th, > td {
					display: block;
					width: 100%;
				}
				> th {
					padding-bottom: 8px;
				}
				> td {
					padding-top: 0;
					border-top: 0;
				}
			}
		}
	}
}

.table_clear {
	width: 100%;
	tr {
		+ tr {
			border-top: 1px solid map-get($color, 'border');
		}
	}
	th, td {
		padding: 15px 0;
	}
	th {
		width: 150px;
		padding-right: 30px;
		white-space: nowrap;
		color: map-get($color, 'dgray');
		font-weight: normal;
		vertical-align: top;
		letter-spacing: 1px;
	}

	@include mq(xs) {
		th, td {
			padding: 12px 0;
		}
		th {
			padding-bottom: 5px;
			line-height: 1.2;
		}
		td {
			padding-top: 0;
			line-height: 1.5;
		}
	}
}

.plan_detail {
	width: 100%;
	display: table;
	> * {
		display: table-cell;
		vertical-align: top;
	}

	& + .plan_detail {
		margin-top: 45px;
	}

	> .img {
		width: 36%;
		padding-right: 5%;
	}
	> .contents {
		> .title {
			margin-bottom: 15px;
			padding: 20px 30px;
			background-color: map-get($color, 'ltgray');
			font-size: 20px;
			line-height: 1.2;
			letter-spacing: 1px;
		}
		> .price {
			margin-top: 20px;
			margin-bottom: 0;
			text-align: right;
			font-weight: bold;
			font-size: 16px;
			color: map-get($color, 'dgray');
			line-height: 1.2;
			letter-spacing: 1px;
		}
	}

	@include mq(xs) {
		display: block;
		> * {
			display: block;
		}

		& + .plan_detail {
			margin-top: 30px;
		}

		> .img {
			margin-bottom: 15px;
			width: 100%;
			padding-right: 0;
		}
		> .contents {
			> .title {
				margin-bottom: 10px;
				padding: 15px;
				font-size: 16px;
			}
			> .price {
				margin-top: 10px;
				margin-bottom: 0;
				font-size: 14px;
			}
		}
	}
}