/* WordPress Native Styles */
strong {
  font-weight: bold;
}

em {
  font-style: italic;
}

blockquote {
  display: block;
  -webkit-margin-before: 1em;
  -webkit-margin-after: 1em;
  -webkit-margin-start: 40px;
  -webkit-margin-end: 40px;
}

img.alignleft {
  margin: 0 auto 0 0;
}

img.aligncenter {
  display: block;
  margin: 0 auto;
}

img.alignright {
  display: block;
  margin: 0 0 0 auto;
}

img[class*="wp-image-"],
img[class*="attachment-"] {
  height: auto;
  max-width: 100%;
}

/*ALL*/
body {
  color: #111;
  font-size: 16px;
  line-height: 1.8;
  font-family: "Noto Sans JP", "游ゴシック Medium", "Yu Gothic Medium", "游ゴシック", "Yu Gothic", "游ゴシック体", "YuGothic", "Hiragino Kaku Gothic ProN", "ヒラギノ角ゴ ProN W3", Meiryo, "メイリオ", Osaka, "MS PGothic", arial, helvetica, sans-serif;
  -webkit-text-size-adjust: 100%;
}

@media all and (-ms-high-contrast: none) {
  *::-ms-backdrop,
  body {
    font-family: "Noto Sans JP", Meiryo, "メイリオ", "Hiragino Kaku Gothic ProN", "ヒラギノ角ゴ ProN W3", Osaka, "MS PGothic", arial, helvetica, sans-serif;
  }
}

a,
a::before,
a::after {
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  -moz-transition: all 0.3s;
  transition: all 0.3s;
}

a,
a:hover,
a:focus {
  text-decoration: none;
}

a:hover {
  opacity: 0.7;
}

a.i_block {
  vertical-align: bottom;
}

/*pagenation*/
.pagination {
  display: block;
  position: relative;
  margin-top: 45px;
  margin-bottom: 0;
  font-size: 16px;
  line-height: 13px;
  text-align: center;
  font-weight: bold;
}

@media screen and (max-width: 767px) {
  .pagination {
    margin-top: 30px;
  }
}

.pagination span,
.pagination a {
  display: inline-block;
  margin: 0 3px;
  padding: 12px;
  background: #fff;
  border: 1px solid #aaa;
  color: #111;
  line-height: 1;
}

.pagination a:hover {
  background-color: #ddd;
}

.pagination .current {
  padding: 12px;
  color: #fff;
  background-color: #aaa;
  border: 1px solid #aaa;
}

