.contact_table.table tr > th, .contact_table.table tr > td {
  padding: 15px 12px;
}

.contact_table.table tr > th {
  letter-spacing: 1px;
  white-space: nowrap;
}

.contact_table.table tr > td .form-control {
  border-color: #ddd;
}

@media screen and (max-width: 767px) {
  .contact_table.table {
    margin-bottom: 20px;
  }
  .contact_table.table tr > th, .contact_table.table tr > td {
    display: block;
    width: 100%;
  }
  .contact_table.table tr > th {
    padding-bottom: 8px;
  }
  .contact_table.table tr > td {
    padding-top: 0;
    border-top: 0;
  }
}

.table_clear {
  width: 100%;
}

.table_clear tr + tr {
  border-top: 1px solid #ddd;
}

.table_clear th, .table_clear td {
  padding: 15px 0;
}

.table_clear th {
  width: 150px;
  padding-right: 30px;
  white-space: nowrap;
  color: #aaa;
  font-weight: normal;
  vertical-align: top;
  letter-spacing: 1px;
}

@media screen and (max-width: 767px) {
  .table_clear th, .table_clear td {
    padding: 12px 0;
  }
  .table_clear th {
    padding-bottom: 5px;
    line-height: 1.2;
  }
  .table_clear td {
    padding-top: 0;
    line-height: 1.5;
  }
}

.plan_detail {
  width: 100%;
  display: table;
}

.plan_detail > * {
  display: table-cell;
  vertical-align: top;
}

.plan_detail + .plan_detail {
  margin-top: 45px;
}

.plan_detail > .img {
  width: 36%;
  padding-right: 5%;
}

.plan_detail > .contents > .title {
  margin-bottom: 15px;
  padding: 20px 30px;
  background-color: #eee;
  font-size: 20px;
  line-height: 1.2;
  letter-spacing: 1px;
}

.plan_detail > .contents > .price {
  margin-top: 20px;
  margin-bottom: 0;
  text-align: right;
  font-weight: bold;
  font-size: 16px;
  color: #aaa;
  line-height: 1.2;
  letter-spacing: 1px;
}

@media screen and (max-width: 767px) {
  .plan_detail {
    display: block;
  }
  .plan_detail > * {
    display: block;
  }
  .plan_detail + .plan_detail {
    margin-top: 30px;
  }
  .plan_detail > .img {
    margin-bottom: 15px;
    width: 100%;
    padding-right: 0;
  }
  .plan_detail > .contents > .title {
    margin-bottom: 10px;
    padding: 15px;
    font-size: 16px;
  }
  .plan_detail > .contents > .price {
    margin-top: 10px;
    margin-bottom: 0;
    font-size: 14px;
  }
}
